<template>
  <b-container class="mt-4">
    <b-row>
      <b-spinner v-if="loading" variant="primary" label="Spinning"></b-spinner>

      <b-col sm="12" v-else>
        <BreadCrumbs v-bind="{ ...total_articles[0] }" />
        <h4 class="text-capitalize border-bottom pb-3" v-if="total_articles.length">{{ total_articles[0].section_name }}</h4>
        <div v-for="article in total_articles" :key="article.id">
          <span class="fe fe-chevron-right mr-2 "></span>
          <b-link @click="showArticle(article.name, article.id)">{{ article.name.trim() }}</b-link>
        </div>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
  import Articles from "@/js/support/cname/articles";
  import BreadCrumbs from "./BreadCrumbs";
  export default {
    data() {
      return {
        total_articles: "",
        loading: false,
      };
    },
    components: { BreadCrumbs },
    methods: {
      // Navigate to selected article
      showArticle(name, article_id) {
        this.$router.push({
          name: "Articles",
          params: {
            article_name: name
              .trim()
              .toLowerCase()
              .replaceAll(" ", "-"),
            a_id: article_id,
            c_id: this.$route.params.c_id,
            s_id: this.$route.params.s_id,
          },
        });
      },

      // Get total articles corresponding to selected category & section
      async getArticles(category_id, section_id) {
        this.total_articles = await Articles.get_Articles(category_id, section_id);
      },
    },
    async mounted() {
      this.loading = true;
      await this.getArticles(this.$route.params.c_id, this.$route.params.s_id);
      this.loading = false;
    },
  };
</script>
